.DataBox {
  background-image: -webkit-gradient(linear, left top, left bottom, from(rgba(34, 96, 138, 0.6)), to(rgba(13, 163, 113, .6)));
  background-image: -webkit-linear-gradient(rgba(34, 96, 138, 0.6), rgba(13, 163, 113, .6));
  background-image: -o-linear-gradient(rgba(34, 96, 138, 0.6), rgba(13, 163, 113, .6));
  background-image: linear-gradient(rgba(34, 96, 138, 0.6), rgba(13, 163, 113, .6));
  width: 80%;
  height: 25rem;
  margin-bottom: 2rem;
  -webkit-box-shadow: 0 1rem 3rem rgba(0,0,0,.5);
          box-shadow: 0 1rem 3rem rgba(0,0,0,.5);
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: distribute;
      justify-content: space-around;
}

@media (min-width: 600px) {
  .DataBox {
    width: 65%;
  }
}

@media (min-width: 768px) {
  .DataBox {
    width: 30%;
    max-width: 450px;
    margin-bottom: 0;
  }
}


